body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background: linear-gradient(to bottom right, #66ccff 0%, #ffcc00 100%) no-repeat;
  background-attachment: fixed;
}

.App{
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

}

/********
Nav 
*********/

.nav{
  margin: 50px 0 50px 0;
  cursor:pointer;
  font-size:20px;
  text-align:center;
  color:white;
}
.nav:hover{
  text-decoration: underline;
  color:black;
}

/**********************
Main page - Quote box 
**********************/
#quote-box{
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items:center;
          align-items:center;
  -webkit-justify-content:center;
          justify-content:center;
  width:500px;
  /*margin-left:auto;
  margin-right:auto; */
}
.quoteCSS{
  border:solid 1px white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction:column;
          flex-direction:column;
  padding:40px;
  box-shadow: 2px,5px;
  -webkit-align-self: center;
          align-self: center; 
  box-shadow: inset 0 0 30px #FFFF;
}

#text{
  font-size:24px;
  text-align:center;
  line-height: 1.4;
  font-family:Helvetica;
}

#author{
  font-size:16px;
  color:black;
  text-align:center;
  padding-top:20px;
  font-family:Helvetica;

}

/******************************
Submit new quote section
*******************************/
.formCSS{
  display:-webkit-flex;
  display:flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}
.input-section{
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction:column;
          flex-direction:column;
}
.inputArea{
   background-color:transparent;
   font-size:20px;
   border:white 1px solid;
   width:500px;
   box-shadow: inset 0 0 30px #FFFF;
}
label{
  font-size:16px;
  padding:10px 0 10px 0;
}

.msg{
  color:white;
  font-weight:bold;
}

/****************************************
Submit button - on Submit new quote page
*****************************************/
.btn-quote{
  width:100px;
  background-color:transparent;
  padding:10px;
  margin:20px;
  border:none;
  box-shadow: inset 0 0 30px #FFFF;
  font-size:14px;
}

.btn-quote:hover{
  opacity:0.6;
}
/**************
Animation class
*****************/

.effect_fade{
  -webkit-animation:fadeIn 3s;
          animation:fadeIn 3s;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;

}

@-webkit-keyframes fadeIn {
  0%   {opacity: 0; }
  50%  {opacity: .5 ;}
  100% { opacity: 1; }
}

@keyframes fadeIn {
  0%   {opacity: 0; }
  50%  {opacity: .5 ;}
  100% { opacity: 1; }
}

